.containerThanks {
  padding: 132px 8%;
  max-width: 1440px;
  min-height: 70px;
  margin: 0 auto;
}

.ticket{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px dashed gray;
    padding: 24px;
    gap: 4px;
}

.circleCheck{
    padding: 32px;
    border-radius: 132px;
    background-color: rgb(20, 156, 93);
}

.checkForm{
    font-size: 19rem;
    color: rgb(204, 200, 200);
}

.descriptionWrapper{
    max-width: 620px;}

.descriptionWrapper h2{
    font-size: 2.2rem;
    margin-bottom: 18px;
}

.descriptionWrapper p{
    font-size: 1.5rem;
}

@media screen and (max-width: 666px){

    .containerThanks {
        padding: 62px 6%;
      }

    .ticket{
       flex-direction: column;
        gap: 36px;
    }

    .descriptionWrapper{
        text-align: center;
    }
    .circleCheck{
        padding: 18px;
        border-radius: 112px;
    }

    .descriptionWrapper h2{
        font-size: 1.8rem;
        margin-bottom: 18px;
    }
    
    .descriptionWrapper p{
        font-size: 1.4rem;
    }
    
}
