.group {
  margin-left: 12px;
}
.group span {
  display: block;
  font-size: 1.4rem;
  padding: 12px 0px 12px 6px;
  margin-bottom: 5px;
  letter-spacing: 0.2px;
  color: rgb(55, 54, 54);
  font-weight: 700;
}

.group span:hover {
  background-color: #f8f7f7;
}

.dropDawn {
    display: none;
    transition: .2s ease;
}

.dropDawnMobile {
  padding: 3px 0px;
}

.dropDawnMobile li a {
  padding: 5px 0px 5px 8px;
  font-size: 1.3rem;
  color: rgb(85, 82, 82);
}

.dropDawnMobile li a:hover {
  color: rgb(30, 178, 109);
  background-color: #f8f7f7;
}

.selected{
  display: block;
}