.containerDefault {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0% 2%;
}
.containerDefault:nth-of-type(1){
  background: rgb(32, 184, 113);
  padding: 2% 4%;
}

.containerDefault:nth-of-type(1) p{
  color: #fff;
  font-size: 1.3rem;
  margin-top: 12px;
}

.contactContain {
  display: flex;
  margin-top: 64px;
}

.infoContain{
    max-width: 22%;
    display: flex;
    flex-direction: column;
    padding: 0px 16px;
    gap: 8px;
}

.infoContain h2{
    font-size: 1.8rem;
    margin-bottom: 18px;
    text-align: center;
    color: rgb(34, 175, 109);
    letter-spacing: .8px;
}

.infoContain span{
    margin-top: 16px;
    font-size: 1.3rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    background-color: rgb(34, 175, 109);
    color: #fff;
    padding: 6px;
    border-radius: 4px;
}

.iconColumn{
    width: 28px;
    height: 28px;
    margin: 0 auto;
    
}

.formContain {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
}

input {
  width: 90%;
  height: 42px;
  font-size: 1.3rem;
  color: gray;
  padding: 4px;
  border: none;
  font-family: 'HarmonyOS Sans';
}

select {
  width: calc(100% - (4 * 3px));
  height: 42px;
  font-size: 1.3rem;
  color: gray;
  padding: 4px;
  margin-bottom: 16px;
  border: 2px solid rgb(104, 102, 102);
  font-family: 'HarmonyOS Sans';
  border-radius: 3px;
}

select > option{
    font-family: 'HarmonyOS Sans';
    font-size: 1.3rem;
    padding: 6px;
}

.w50 {
  width: calc(50% - (4 * 3px));
  margin: 12px 0px;
  display: flex;
  align-items: center;
  border: 2px solid rgb(104, 102, 102);
  border-radius: 3px;
  margin-left: 3px;
}

.icon {
  width: 5%;
  height: 60%;
  color: rgb(12, 124, 72);
}

textarea {
  width: calc(100% - (4 * 3px));
  padding: 12px;
  font-size: 1.3rem;
  border: 2px solid rgb(104, 102, 102);
  font-family: 'HarmonyOS Sans';
  border-radius: 3px;
}

.w100{
  width: 100%;
}

.btnSubmit{
  margin-top: 26px;
  background-color: rgb(51, 197, 129);
  color: white;
  width: 100%;
}

@media screen and (max-width: 1160px){

    .contactContain {
        flex-direction: column;
        margin-top: 18px;
      }

      .infoContain{
       width: 100%;
       max-width: 100%;
      margin-bottom: 12px;
    }

    .infoContain span{
      margin-top: 4px;
      width: 90%;
      margin: 0 auto;
    }

    .formContain{
        width: 80%;
        margin: 0 auto;
    }

    .w50,select,textarea{
        width: 100%;
    }
    .w50{
        
        margin-left: 0;
    }
    input{
        width: 93%;
    }

    

    
}
