.welcomePresetation {
  width: 100%;
  padding: 60px 0px;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
 
}

.welcomePresetation .descriptionPresetation {
  width: 50%;
}

.welcomePresetation .titlePresetation {
  color: #fff;
  font-size: 3.8rem;
}

.welcomePresetation .paragraphPresetation{
  font-size: 1.6rem;
  color: rgba(210, 206, 206, 0.911);
  margin-top: 15px;
}

.welcomePresetation .presetationLogo {
  color: #1e261b;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 7.8rem;
  letter-spacing: 2px;
  -webkit-text-stroke-width: 0.3px;
  -webkit-text-stroke-color: #ffffff06;
}
@media screen and (max-width: 1160px) {
  .welcomePresetation .titlePresetation{
    font-size: 3.8rem;
  }

  .welcomePresetation .paragraphPresetation {
    font-size: 1.6rem;
  }
}

@media screen and (max-width: 862px) {
  .welcomePresetation .descriptionPresetation {
    width: 100%;
  }
  .welcomePresetation .presetationLogo{
    display: none;
  }
   .descriptionPresetation .paragraphPresetation {
    text-align: justify;
  }
}

@media screen and (max-width: 520px) {
  
  .descriptionPresetation .titlePresetation {
    font-size: 2.8rem;
  }
 
}
