.containerDefault {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0% 4%;
}

.containerDefault:nth-of-type(1) {
  background: rgb(32, 184, 113);
  padding: 2% 4%;
}

.count{
    margin-top: 18px;
    margin-left: 12px;
}

.count span {
  font-size: 1.3rem;
  color: rgb(54, 54, 54);
}

.productsContain {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

.productsContain .product {
  height: 232px;
  width: 100%;
  padding: 28px;
  display: flex;
  flex-direction: column;
}

.btnPosition {
  align-self: flex-end;
}

.productsContain .product .productImage {
  width: 100%;
  height: 70%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.descriptionProducts {
  margin: 6px 0px 6px 0px;
}

.descriptionProducts span {
  font-size: 1.3rem;
}

.buttonPages{
  max-width: 95%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 14px;
    margin-top: 32px;
    flex-wrap: wrap;
}

.btnPagination {
  padding: 5px 8px;
  font-size: 1.3rem;
  outline: none;
  border: none;
  background-color: rgb(32, 184, 113);
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
}

@media screen and (max-width: 1060px) {
  .productsContain {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 666px) {
  .productsContain {
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
  }

  .productsContain .product {
    padding: 14px;
  }
  .descriptionProducts span {
    font-size: 1.2rem;
  }
  .btnPagination {
    padding: 8px 10px;
    font-size: 1.4rem;
  }
}
