footer {
  background-color: #555252;
  margin-top: 108px;
}
.containerFooter {
  max-width: 1440px;
  padding: 4% 6%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.description {
  max-width: 220px;
  background-color: #3a3939;
  padding: 20px 12px;
  border-radius: 0px 16px 0px 16px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.description p {
  font-size: 1.2rem;
  text-align: justify;
  margin-top: 12px;
  color: #dfdada;
  max-width: 180px;
}

.linksFooter ul h2,
.addressFooter h2 {
  font-size: 1.6rem;
  text-align: center;
  color: #ffffffc3;
}

.linksFooter ul li {
  margin-top: 14px;
}

.linksFooter ul li a {
  color: #dfdada;
  font-size: 1.4rem;
}

.addressFooter address,
.addressFooter span {
  margin-top: 8px;
  font-size: 1.4rem;
  color: #dfdada;
  display: flex;
  align-items: center;
}

.addressFooter address{
  margin-bottom: 8px;
}

.telephoneFooter,
.locationFooter{
  width: 28px;
  height: 22px;
  padding: 2px;
  color: #fff;
}



.dues {
  text-align: center;
  font-size: 1.3rem;
  font-weight: 400;
  background-color: rgb(42, 41, 41);
  color: #dfdada;
  padding: 14px 0px 14px 16px;
}


@media screen and (max-width: 820px){
  .containerFooter {
    flex-direction: column;
    align-items: center;
    gap: 32px;
    padding: 32px 0;
  }
}