.containerHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 2%;
  max-width: 1440px;
  min-height: 70px;
  margin: 0 auto;
}

.navLeft {
  display: flex;
  align-items: flex-end;
  gap: 18px;
  position: relative;
}

.navLeft ul li a:first-child {
  font-size: 1.4rem;
  color: rgb(28, 28, 28);
  font-weight: 400;
  letter-spacing: 0.2px;
  padding: 7px 14px;
  margin-left: 12px;
  border-radius: 16px;
  transition: 0.1s ease-in;
  cursor: pointer;
}

.navLeft ul li a:first-child:hover {
  transition: 0.1 ease-in;
  background-color: #f8f7f7;
}

.navLeft ul {
  display: flex;
}

.navRight {
  display: flex;
  align-items: flex-end;
  gap: 26px;
}

.select {
  display: block;
}

.twoHeader {
  width: 100%;
  margin-top: 28px;
  height: auto;
}

.twoHeader ul span,
.twoHeader ul li a {
  display: block;
  width: 100%;
}

.twoHeader ul li a:first-child {
  padding-left: 18px;
  padding-bottom: 14px;
  font-weight: 700;
  letter-spacing: .1px;
  font-size: 1.5rem;
  color: rgb(55, 54, 54);
}

.hamburgDisplay {
  display: none;
}

.headerMobile {
  width: 100%;
  display: none;
}

.boxMenu {
  display: flex;
  gap: 32px;
}
.headerMobile .boxOne {
  display: flex;
  justify-content: space-between;
  margin-bottom: 18px;
}

.containerHeader:nth-child(2) {
  display: none;
}
.containerHeader:nth-child(3) {
  display: none;
  transition: .2s ease;
}

@media screen and (max-width: 1160px) {
  .navLeft ul {
    display: flex;
    flex-direction: column;
  }
  .navLeft ul li a:first-child {
    display: none;
  }

  .hamburgDisplay {
    display: block;
    cursor: pointer;
    padding-right: 4px;
  }

  .headerMobile {
    display: block;
  }
  .containerHeader:first-child {
    display: none;
  }
  .containerHeader:nth-child(2) {
    display: block;
  }
}

