.btnBannerCheck {
  width: 338px;
  padding: 19px;
  text-align: center;
  background: rgba(52, 167, 52, 0.932);
  color: #fff;
  font-size: 2.2rem;
}

@media screen and (max-width: 862px) {
  .btnBannerCheck {
    width: 292px;
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 520px) {
  .btnBannerCheck {
    width: 262px;
    font-size: 1.6rem;
  }
}
