.containerDefault{
    max-width: 1440px;
    margin: 0 auto;
    padding: 2% 2%;
}

.flexCenterContain{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 68px;
}

.containerDefault:nth-of-type(1){
    background-color: rgb(32, 184, 113);
}

.whoWhe,.mission{
    width: 60%;
    text-align: center;
    
}

.whoWhe h2,.mission h2{
    font-size: 2.4rem;
    margin-bottom: 18px;
    color: rgb(54,54,54);
}

.whoWhe p, .mission p{
    font-size: 1.4rem;
    color: rgb(82,82,82);
}

.margin{
    margin-bottom: 282px;
}