.group {
  position: relative;
  width: auto;
  z-index: 999;
}

.group:hover > .dropDawn {
  transition: 0.1s ease-in;
  display: block;
}

.group span {
  font-size: 1.4rem;
  color: rgb(28, 28, 28);
  font-weight: 300;
  letter-spacing: 0.2px;
  padding: 7px 22px 7px 14px;
  border-radius: 16px;
  transition: 0.1s ease-in;
  cursor: pointer;
}

.group span:hover {
  transition: 0.1 ease-in;
  background-color: #f8f7f7;
}

.dropDawn {
  position: absolute;
  padding: 12px 6px 12px 0px;
  width: auto;
  display: none;
  transition: 0.1s ease-in-out;
}

.dropDawn .ul {
  display: flex;
  flex-direction: column;
  background-color: rgb(250, 250, 250);
  width: 180px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.dropDawn .ul li a {
  display: block;
  width: 100%;
  font-size: 1.3rem;
  padding: 4px 4px 4px 4px;
  color: #2d2d2d;
  font-weight: 500;
}

.dropDawn .ul li a:hover {
  color: rgb(30, 178, 109);
  background-color: transparent !important;
}

.dropDawn .ul li {
  width: 100%;
  padding: 4px 4px 4px 4px;
}


@media screen and (max-width:1160px) {
    .group{
      display: none;
    }
}
