.productContainer {
  margin-top: 68px;
}
.containerDefault {
  max-width: 1440px;
  padding: 2% 2%;
  margin: 0 auto;
}

.wrapperProduct {
  display: flex;
  align-items: flex-start;
}

.wrapperProduct .productImage {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 48%;
  height: 248px;
}

.description {
  display: flex;
  flex-direction: column;
  max-width: 40%;
}

.description h2 {
  font-size: 2.2rem;
  margin-bottom: 42px;
}

.description p {
  font-size: 1.3rem;
  margin-bottom: 36px;
}

.description span {
  font-size: 1.2rem;
}

.description a {
  display: block;
  width: 80%;
  padding: 12px 28px;
  font-size: 1.5rem;
  color: #fff;
  border-radius: 12px;
  text-align: center;
  background-color: rgb(30, 178, 109);
  margin-top: 26px;
}

.ReleasedContain {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 62px;
}
.product {
  width: 15%;
  border: 16px solid transparent;
  height: 252px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product .productImage {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 100%;
  height: 126px;
}

.product .descriptionProducts {
  font-size: 1.5rem;
  margin-bottom: 4px;
}

.titleWrapper {
  margin-left: 14%;
  margin-top: 92px;
}

@media screen and (max-width: 666px) {
  .ReleasedContain {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .product{
    width: 60%;
    
  }
  .product .descriptionProducts{
    font-size: 1.8rem;
  }

  .wrapperProduct .productImage {
    width: 95%;
    height: 182px;
  }
  .wrapperProduct {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .description {
    display: flex;
    flex-direction: column;
    max-width: 92%;
    margin: 52px 0px 12px 0px;
  }

  .description h2 {
    font-size: 1.8rem;
    margin-bottom: 32px;
  }
  
  .description p {
    font-size: 1.3rem;
    margin-bottom: 36px;
    text-align: justify;
  }

  .description a {
    width: 100%;
  }
  .titleWrapper {
    margin-left: 0px;
    margin-right: 8px;
    margin-top: 52px;
  }

}
