.lineHomeBanner {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 74px 48px;
  max-width: 80vw;
  width: 100%;
  height: 382px;
  left: 50%;
  top: 80%;
  transform: translateX(-50%);
  position: absolute;
  background-color: rgb(30, 178, 109);
}

.descriptionBannerHome {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.descriptionBannerHome h3 {
  font-size: 2.8rem;
  font-weight: 600;
  color: #fff;
  margin-bottom: 56px;
}

.descriptionBannerHome h3 span {
  font-size: 2.8rem;
  color: rgba(236, 236, 33, 0.933);
}

.imageBgHome {
  background-color: rgb(61, 211, 61);
  width: 332px;
  height: 312px;
  border-radius: 262px;
  position: relative;
}

.imageProductsHome {
  background: url("../../public/images/banner/vetorhomepage-removebg.png")
    center center no-repeat;
  background-size: cover;
  width: 96%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 1160px) {
  .lineHomeBanner {
    display: flex;
    flex-direction: column;
    height: 732px;
    gap: 82px;
    padding: 64px 32px;
    width: 72vw;
    top: 80%;
  }
  .descriptionBannerHome {
    width: 74%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .imageProductsHome {
    background-size: 180%;
  }
}

@media screen and (max-width: 862px) {
  .descriptionBannerHome {
    width: 82%;
    text-align: justify;
  }

  .descriptionBannerHome h3 {
    margin-bottom: 52px;
  }
  .descriptionBannerHome h3,
  .descriptionBannerHome h3 span {
    font-size: 2.4rem;
  }

  .imageProductsHome {
    background-size: 160%;
  }

  .imageBgHome {
    width: 342px;
    height: 338px;
    border-radius: 262px;
  }
}

@media screen and (max-width: 520px) {
  .descriptionBannerHome {
    width: 100%;
  }

  .descriptionBannerHome h3,
  .descriptionBannerHome h3 span {
    font-size: 2rem;
    text-align: center;
  }

  .imageProductsHome {
    background-size: 150%;
  }

  .imageBgHome {
    min-width: 238px;
    max-width: 292px;
    width: 100%;
    max-height: 242px;
    min-height: 212px;
    height: 100%;
    border-radius: 262px;
  }

  .lineHomeBanner {
    padding: 52px 34px;
    max-width: 84vw;
    height: auto;
    width: 100%;
    justify-content: flex-start;
  }
}
