.boxBrand{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 18px;
    flex-wrap: wrap;
  }
  
  .boxBrand .brandImage{
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 138px;
    height: 90px;
  }
  
  @media screen and (max-width: 532px){
    .boxBrand .brandImage{
        width: 118px;
        height: 78px;
       
      }

      .brandDisplay{
        display: none;
      }
      
  }
  