.carousel{
  position: relative;
}
.carouselWrapper{
    overflow: hidden;
    scroll-behavior: smooth;
    transition: .2s ease-in-out; 
    padding-top: 76px;
  }

  .inner{
    width: 100%;
    display: flex;
    margin: 0 auto;
    padding-right: 16px;
  }
  
  .product{
    min-width: 18%;
    height: 200px;
    margin: 0px 1.2rem;
    display: flex;
    flex-direction: column;
  }

  .productImage{
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 132px;
  }
  
  .product .descriptionProducts{
    margin-top: 12px;
  }
  .product span{
    font-size: 1.4rem;
    text-align: center;
    font-weight: 400;
    display: block;
    color: rgb(50, 50, 50);
    margin-bottom: 8px;
  }
  
  

  .arrowRight,
  .arrowLeft {
      width: 40px;
      height: 40px;
      position: absolute;
      padding: 4px;
      border: 2px solid rgb(53, 196, 53);
      border-radius: 8px;
      cursor: pointer;
      color: rgb(47, 168, 47);
      transition: .2s ease;
  }

  .arrowLeft:hover,
  .arrowRight:hover{
    transition: .2s ease-in;
    background-color: rgb(47, 168, 47);
    color: #fff;
  }

  .arrowLeft{
    transform: rotate(180deg);
    right: 5%;
      top: 0;
  }

  .arrowRight{
    right: 0;
    top: 0;
  }

  @media screen and (max-width: 1160px){
    .product{
      min-width: 22.5%;
    }

    .arrowLeft{
      right: 8%;
    }
  }
  

  @media screen and (max-width: 763px){
    .product{
      min-width: 30%;
    }

    .arrowLeft{
      right: 10%;
    }

    .product span{
      font-size: 1.3rem;
      text-align: center;
    }

    .btnProduct{
      padding: 8px 6px;
      font-size: 1.4rem;
      align-content: flex-end;
    }
  }

  
  @media screen and (max-width: 532px){
    .product{
      min-width: 46%;
    }
    .arrowRight,
    .arrowLeft {
     display: none;
    }
  }


  