.searchBox {
  display: flex;
  align-items: center;
  border: 2px solid rgb(30, 178, 109);
  height: 30px;
  width: 236px;
}

.searchBox .searchInput {
  border: none;
  padding: 0px 2%;
  font-size: 1.4rem;
  height: 100%;
  line-height: 18px;
  width: 88%;
  gap: 4px;
}

.searchBox button{
    border: none;
    padding: 4px;
    border-radius: 2px;
    background-color: rgb(30, 178, 109);
}

.searchBox  .searchInput::placeholder {
  font-size: 1.3rem;
}

.searchBox  .loupe {
  background: url(../../public/images/icons/loupe.svg) center center no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin: 0 auto;
}

@media screen and (max-width: 1160px){
  .searchBox{
    width: 50%;
    margin: 0 auto;
    border: 3px solid rgb(30, 178, 109);
  }

  .searchBox button{
    width: 12%;
  }
}

@media screen and (max-width: 650px){
  .searchBox{
    width: 80%;
    height: 36px;
    margin: 0 auto;
    border-radius: 4px;
    border: 3px solid rgb(30, 178, 109);
    overflow: hidden;
  }

  .searchBox  .loupe {
    width: 20px;
    height: 26px;
    border-radius: 4px;
    cursor: pointer;
    margin: 0 auto;
  }
}

