.boxTitle{
  position: relative;
  width: max-content;
}

.boxTitle .line{
    width: 50%;
    height: 4px;
    border-radius: 4px;
    background-color: rgb(30, 178, 109);
    margin: 0 auto;
}

.boxTitle h2{
  font-size: 2.2rem;
  padding-bottom: 3px;
  color: rgb(50,50,50);
  
}

@media screen and (max-width: 532px){
  .boxTitle{
    width: initial;
  }

  .boxTitle h2{
    text-align: center;
    font-size: 1.8rem;
  }

  .boxTitle .line{
    width: 40%;
  }
}